<template>
  <div class="analysis-detail-page">
    <div class="page-header">
      <button @click="goBack" class="back-button">
        <span class="back-icon">←</span>
        返回
      </button>
      <h1 class="page-title">分析详情</h1>
    </div>

    <div v-if="loading" class="loading-container">
      <div class="loading-spinner"></div>
      <p>正在加载分析结果...</p>
    </div>

    <div v-else-if="error" class="error-container">
      <div class="error-icon">⚠️</div>
      <h3>加载失败</h3>
      <p>{{ error }}</p>
      <button @click="fetchAnalysis" class="retry-button" :disabled="isRetrying">
        <span v-if="isRetrying">重试中...</span>
        <span v-else>重试</span>
      </button>
    </div>

    <div v-else-if="analysis" class="analysis-content">
      <!-- 图片展示区域 -->
      <div class="image-section">
        <div class="image-container">
          <img 
            :src="analysis.image_url" 
            :alt="analysis.filename || '服装图片'" 
            class="analysis-image"
            @error="handleImageError"
          >
        </div>
        <div class="image-info">
          <h3>{{ analysis.filename || '未知文件名' }}</h3>
          <p class="upload-time">上传时间: {{ formatDate(analysis.created_at) }}</p>
        </div>
      </div>

      <!-- 服装分析区域 -->
      <div class="analysis-section">
        <h2 class="section-title">
          <span class="section-icon">👗</span>
          服装分析
        </h2>
        
        <!-- 整体概述 -->
        <div v-if="analysisData.overview" class="analysis-card">
          <h3 class="card-title">整体概述</h3>
          <div class="card-content" v-html="analysisData.overview"></div>
        </div>

        <!-- 单品清单 -->
        <div v-if="clothesInImage && clothesInImage.length > 0" class="analysis-card">
          <h3 class="card-title">识别到的服装单品</h3>
          <div class="clothes-list">
            <div v-for="(item, index) in clothesInImage" :key="index" class="clothes-item">
              <span v-html="formatClothingItem(item)"></span>
              <a href="https://www.taobao.com" target="_blank" class="inline-product-link">
                查看商品
              </a>
            </div>
          </div>
        </div>

        <!-- 场景识别 -->
        <div v-if="analysisData.scene_analysis" class="analysis-card">
          <h3 class="card-title">场景识别</h3>
          <div class="card-content" v-html="analysisData.scene_analysis"></div>
        </div>

        <!-- 场景即时建议 -->
        <div v-if="outfitScenes && Object.keys(outfitScenes).length > 0" class="analysis-card">
          <h3 class="card-title">场景即时建议</h3>
          <div class="scenes-container">
            <div v-for="(items, scene) in outfitScenes" :key="scene" class="scene-group">
              <h4 class="scene-title">{{ scene }}</h4>
              <div class="scene-items">
                <span v-for="(item, index) in items" :key="index" class="scene-item">
                  {{ item }}
                </span>
              </div>
            </div>
          </div>
        </div>

        <!-- 其他分析内容 -->
        <div v-if="analysisData.style_analysis" class="analysis-card">
          <h3 class="card-title">风格分析</h3>
          <div class="card-content" v-html="analysisData.style_analysis"></div>
        </div>

        <div v-if="analysisData.color_analysis" class="analysis-card">
          <h3 class="card-title">色彩分析</h3>
          <div class="card-content" v-html="analysisData.color_analysis"></div>
        </div>

        <div v-if="analysisData.fit_analysis" class="analysis-card">
          <h3 class="card-title">版型分析</h3>
          <div class="card-content" v-html="analysisData.fit_analysis"></div>
        </div>
      </div>

      <!-- 穿搭推荐区域 -->
      <div class="recommendation-section">
        <h2 class="section-title">
          <span class="section-icon">✨</span>
          穿搭推荐
        </h2>

        <!-- 人物特征 -->
        <div v-if="recommendationData.character_analysis" class="analysis-card">
          <h3 class="card-title">人物特征</h3>
          <div class="card-content" v-html="recommendationData.character_analysis"></div>
        </div>

        <!-- 分场景推荐 -->
        <div v-if="recommendationData.scene_recommendations" class="analysis-card">
          <h3 class="card-title">分场景推荐</h3>
          <div class="card-content" v-html="recommendationData.scene_recommendations"></div>
        </div>

        <!-- 配色与层次 -->
        <div v-if="recommendationData.color_coordination" class="analysis-card">
          <h3 class="card-title">配色与层次</h3>
          <div class="card-content" v-html="recommendationData.color_coordination"></div>
        </div>

        <!-- 其他推荐内容 -->
        <div v-if="recommendationData.accessory_suggestions" class="analysis-card">
          <h3 class="card-title">配饰建议</h3>
          <div class="card-content" v-html="recommendationData.accessory_suggestions"></div>
        </div>

        <div v-if="recommendationData.occasion_matching" class="analysis-card">
          <h3 class="card-title">场合搭配</h3>
          <div class="card-content" v-html="recommendationData.occasion_matching"></div>
        </div>
      </div>
    </div>

    <div v-else class="no-data-container">
      <div class="no-data-icon">📷</div>
      <h3>暂无分析数据</h3>
      <p>请先上传图片进行分析</p>
      <router-link to="/upload" class="upload-link">去上传图片</router-link>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'AnalysisDetail',
  data() {
    return {
      analysis: null,
      loading: true,
      error: null,
      isRetrying: false,
      analysisData: {},
      recommendationData: {},
      clothesInImage: [],
      outfitScenes: {},
      // 用于图片加载失败时的备用图
      fallbackImage: 'https://picsum.photos/300/400?grayscale&blur=2'
    }
  },
  async created() {
    // 尝试从缓存加载数据
    const cachedData = this.getCachedData()
    if (cachedData) {
      this.analysis = cachedData
      this.updateFormattedData()
      this.loading = false
    } else {
      await this.fetchAnalysis()
    }
  },
  methods: {
    async fetchAnalysis() {
      const analysisId = this.$route.params.id
      if (!analysisId) {
        this.error = '缺少分析ID'
        this.loading = false
        return
      }

      try {
        this.loading = true
        this.error = null
        this.isRetrying = true
        
        const response = await axios.get(`/api/analysis/${analysisId}`)
        this.analysis = response.data
        
        // 缓存数据
        this.cacheData(response.data)
        
        // 更新分析和推荐数据
        this.updateFormattedData()
        
      } catch (error) {
        console.error('获取分析详情失败:', error)
        this.error = error.response?.data?.error || '获取分析详情失败，请稍后重试'
      } finally {
        this.loading = false
        this.isRetrying = false
      }
    },

    // 缓存数据到sessionStorage
    cacheData(data) {
      const analysisId = this.$route.params.id
      if (analysisId && data) {
        try {
          sessionStorage.setItem(`analysis_${analysisId}`, JSON.stringify(data))
        } catch (e) {
          console.warn('数据缓存失败:', e)
        }
      }
    },

    // 从缓存获取数据
    getCachedData() {
      const analysisId = this.$route.params.id
      if (!analysisId) return null
      
      try {
        const cached = sessionStorage.getItem(`analysis_${analysisId}`)
        return cached ? JSON.parse(cached) : null
      } catch (e) {
        console.warn('获取缓存数据失败:', e)
        return null
      }
    },

    // 更新分析和推荐数据
    updateFormattedData() {
      if (this.analysis && this.analysis.clothing_analysis) {
        let clothingAnalysis = this.analysis.clothing_analysis;

        // Defend against double-encoded JSON
        if (typeof clothingAnalysis === 'string') {
          try {
            clothingAnalysis = JSON.parse(clothingAnalysis);
          } catch (e) {
            console.error('Failed to parse clothing_analysis string:', e);
            // If parsing fails, it might be the old raw text format
            this.analysisData = { overview: clothingAnalysis };
            return;
          }
        }
        
        // Check for new structured data format
        if (clothingAnalysis.analysis_data && clothingAnalysis.recommendation_data) {
          this.analysisData = {
            overview: clothingAnalysis.analysis_data.overview,
            scene_analysis: clothingAnalysis.analysis_data.scene_analysis,
            scene_suggestion: clothingAnalysis.analysis_data.scene_suggestion
          };
          
          this.recommendationData = {
            character_analysis: clothingAnalysis.recommendation_data.character_analysis,
            scene_recommendations: clothingAnalysis.recommendation_data.scene_recommendations.join('<br>'),
            color_coordination: clothingAnalysis.recommendation_data.color_coordination
          };
          
          this.clothesInImage = clothingAnalysis.clothes_in_image || [];
          this.outfitScenes = clothingAnalysis.outfit_scenes || {};
        } else if (clothingAnalysis.detailed_analysis) {
          // Fallback for old format
          const detailedAnalysis = clothingAnalysis.detailed_analysis;
          this.extractStructuredData(detailedAnalysis);
          const allClothingNames = this.collectAllClothingNames();
          this.analysisData = this.formatAnalysisText(detailedAnalysis, allClothingNames);
          this.recommendationData = this.formatRecommendationText(detailedAnalysis, allClothingNames);
        } else {
            // Final fallback: display the raw object if no known structure matches
            this.analysisData = { overview: JSON.stringify(clothingAnalysis, null, 2) };
        }
      }
    },

    // 收集所有唯一的服装名称
    collectAllClothingNames() {
      const clothingNames = new Set();
      
      // 从 CLOTHES_IN_IMAGE 数组中收集
      if (this.clothesInImage && Array.isArray(this.clothesInImage)) {
        this.clothesInImage.forEach(item => {
          if (item && typeof item === 'string' && item.trim()) {
            clothingNames.add(item.trim());
          }
        });
      }
      
      // 从 OUTFIT_SCENES 对象中收集
      if (this.outfitScenes && typeof this.outfitScenes === 'object') {
        Object.values(this.outfitScenes).forEach(sceneItems => {
          if (Array.isArray(sceneItems)) {
            sceneItems.forEach(item => {
              if (item && typeof item === 'string' && item.trim()) {
                clothingNames.add(item.trim());
              }
            });
          }
        });
      }
      
      console.log('收集到的所有服装名称:', Array.from(clothingNames));
      return Array.from(clothingNames);
    },

    // 将文本中的服装名称替换为超链接
    replaceClothingNamesWithLinks(text, clothingNames) {
      if (!text || !clothingNames || clothingNames.length === 0) {
        return text;
      }
      
      let processedText = text;
      
      // 按长度降序排序，优先匹配较长的名称，避免短名称覆盖长名称
      const sortedNames = [...clothingNames].sort((a, b) => b.length - a.length);
      
      sortedNames.forEach(clothingName => {
        if (clothingName && clothingName.length > 1) { // 避免匹配过短的词
          // 转义待匹配名称中的正则特殊字符
          const escaped = clothingName.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
          // 匹配前一位不是字母数字的情况，避免英文串中间误匹配；中文不使用\b
          const regex = new RegExp(`(^|[^A-Za-z0-9])(${escaped})(?![A-Za-z0-9])`, 'g');
          
          // 替换为占位符，保留前导分组
          const placeholder = `__CLOTHING_LINK_${Date.now()}_${Math.random().toString(36).substr(2, 9)}__`;
          processedText = processedText.replace(regex, `$1${placeholder}`);
          
          // 将占位符替换为实际的超链接
          const linkHtml = `<a href="about:blank" target="_blank" class="clothing-name-link">${clothingName}</a>`;
          processedText = processedText.replace(new RegExp(placeholder, 'g'), linkHtml);
        }
      });
      
      return processedText;
    },

    formatAnalysisText(text, clothingNames = []) {
      console.log('formatAnalysisText input:', text);
      if (!text) {
        return {};
      }
      
      if (typeof text === 'string' && text.trim()) {
        // 移除结构化数组输出部分及其后的所有内容
        let processedText = text.replace(/\[结构化数组输出\][\s\S]*$/g, '');
        
        // 将服装名称替换为超链接
        processedText = this.replaceClothingNamesWithLinks(processedText, clothingNames);
        
        let formattedText = {};
        
        // 分割不同部分 - 增强版正则，支持更多格式
        const sections = processedText.split(/(?=\s*\d+\.\s*[^\d])/);
        
        sections.forEach(section => {
          const trimmedSection = section.trim();
          if (!trimmedSection) return;
          
          if (trimmedSection.includes('整体概述') || trimmedSection.includes('概述')) {
            formattedText.overview = this.formatSectionContent(trimmedSection);
          } else if (trimmedSection.includes('场景识别') || trimmedSection.includes('场景分析')) {
            formattedText.scene_analysis = this.formatSectionContent(trimmedSection);
          } else if (trimmedSection.includes('风格分析') || trimmedSection.includes('风格')) {
            formattedText.style_analysis = this.formatSectionContent(trimmedSection);
          } else if (trimmedSection.includes('色彩分析') || trimmedSection.includes('颜色')) {
            formattedText.color_analysis = this.formatSectionContent(trimmedSection);
          } else if (trimmedSection.includes('版型分析') || trimmedSection.includes('剪裁')) {
            formattedText.fit_analysis = this.formatSectionContent(trimmedSection);
          }
        });
        
        // 如果没有解析到内容，使用原始文本作为整体概述
        if (Object.keys(formattedText).length === 0) {
          formattedText.overview = processedText;
        }
        
        return formattedText;
      }
      
      return {};
    },

    formatRecommendationText(text, clothingNames = []) {
      console.log('formatRecommendationText input:', text);
      if (!text) {
        return {};
      }
      
      if (typeof text === 'string' && text.trim()) {
        // 移除结构化数组输出部分及其后的所有内容
        let processedText = text.replace(/\[结构化数组输出\][\s\S]*$/g, '');
        
        // 将服装名称替换为超链接
        processedText = this.replaceClothingNamesWithLinks(processedText, clothingNames);
        
        let formattedText = {};
        
        // 分割不同部分 - 增强版正则
        const sections = processedText.split(/(?=\s*\d+\.\s*[^\d])/);
        
        sections.forEach(section => {
          const trimmedSection = section.trim();
          if (!trimmedSection) return;
          
          if (trimmedSection.includes('人物特征') || trimmedSection.includes('个人特点')) {
            formattedText.character_analysis = this.formatSectionContent(trimmedSection);
          } else if (trimmedSection.includes('场景推荐') || trimmedSection.includes('搭配建议')) {
            formattedText.scene_recommendations = this.formatSectionContent(trimmedSection);
          } else if (trimmedSection.includes('配色') || trimmedSection.includes('层次')) {
            formattedText.color_coordination = this.formatSectionContent(trimmedSection);
          } else if (trimmedSection.includes('配饰') || trimmedSection.includes('饰品')) {
            formattedText.accessory_suggestions = this.formatSectionContent(trimmedSection);
          } else if (trimmedSection.includes('场合') || trimmedSection.includes('适用')) {
            formattedText.occasion_matching = this.formatSectionContent(trimmedSection);
          }
        });
        
        return formattedText;
      }
      
      return {};
    },

    // 提取结构化数组数据 - 增强版，支持更多格式
    extractStructuredData(text) {
      if (!text || typeof text !== 'string') {
        return;
      }

      // 提取 CLOTHES_IN_IMAGE 数组 - 支持单引号、双引号和不同空格格式
      const clothesMatch = text.match(/CLOTHES_IN_IMAGE\s*=\s*\[\s*([\s\S]*?)\s*\]/);
      if (clothesMatch) {
        try {
          // 解析数组内容，处理引号和逗号
          const clothesStr = clothesMatch[1];
          // 分割时考虑可能的空格和换行
          const clothesArray = clothesStr.split(/\s*,\s*/).map(item => 
            item.trim().replace(/^["']|["']$/g, '') // 同时处理单引号和双引号
          ).filter(item => item.length > 0);
          
          this.clothesInImage = clothesArray;
          console.log('提取到的服装数组:', this.clothesInImage);
        } catch (error) {
          console.error('解析 CLOTHES_IN_IMAGE 失败:', error);
          // 失败时提供更友好的降级处理
          this.clothesInImage = [];
        }
      }

      // 提取 OUTFIT_SCENES 对象 - 增强版解析
      const scenesMatch = text.match(/OUTFIT_SCENES\s*=\s*\{([\s\S]*?)\}(?=\s*$|\s*\n\s*[A-Z_]|\s*\])/);
      if (scenesMatch) {
        try {
          const scenesStr = scenesMatch[1];
          const scenes = {};
          
          // 匹配每个场景的键值对 - 支持更多格式
          const sceneMatches = scenesStr.matchAll(/"([^"]+)"\s*:\s*\[\s*([\s\S]*?)\s*\]/g);
          for (const match of sceneMatches) {
            const sceneName = match[1];
            const itemsStr = match[2];
            const items = itemsStr.split(/\s*,\s*/).map(item => 
              item.trim().replace(/^["']|["']$/g, '') // 处理单双引号
            ).filter(item => item.length > 0);
            
            scenes[sceneName] = items;
          }
          
          this.outfitScenes = scenes;
          console.log('提取到的场景对象:', this.outfitScenes);
        } catch (error) {
          console.error('解析 OUTFIT_SCENES 失败:', error);
          // 失败时提供更友好的降级处理
          this.outfitScenes = {};
        }
      }
    },

    formatSectionContent(content) {
      if (!content) return '';
      
      // 移除数字编号开头 - 增强版正则
      content = content.replace(/^\s*\d+\.\s*/, '');
      
      // 转换换行为HTML
      content = content.replace(/\n/g, '<br>');
      
      // 处理粗体标记
      content = content.replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>');
      
      return content;
    },

    // 格式化服装单品，添加链接
    formatClothingItem(item) {
      const allClothingNames = this.collectAllClothingNames();
      return this.replaceClothingNamesWithLinks(item, allClothingNames);
    },

    formatDate(dateString) {
      if (!dateString) return '未知时间';
      try {
        const date = new Date(dateString);
        return date.toLocaleString('zh-CN');
      } catch (e) {
        return dateString;
      }
    },

    // 处理图片加载失败
    handleImageError(e) {
      e.target.src = this.fallbackImage;
    },

    goBack() {
      this.$router.go(-1);
    }
  }
}
</script>

<style lang="scss" scoped>
