      background: rgba(255, 255, 255, 0.2);
      border: none;
      color: white;
      padding: 12px 20px;
      border-radius: 25px;
      cursor: pointer;
      font-size: 16px;
      font-weight: 500;
      transition: $transition;
      backdrop-filter: blur(10px);
      
      &:hover {
        background: rgba(255, 255, 255, 0.3);
        transform: translateX(-2px);
      }
      
      .back-icon {
        font-size: 18px;
      }
    }
    
    .page-title {
      margin-left: 20px;
      color: white;
      font-size: 32px;
      font-weight: 700;
      text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
    }
  }
  
  .loading-container, .error-container, .no-data-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 400px;
    background: $bg-color;
    border-radius: $border-radius-large;
    backdrop-filter: blur(20px);
    box-shadow: $shadow;
    padding: 20px;
    
    .loading-spinner {
      width: 50px;
      height: 50px;
      border: 4px solid #f3f3f3;
      border-top: 4px solid $primary-color-1;
      border-radius: 50%;
      animation: spin 1s linear infinite;
      margin-bottom: 20px;
    }
    
    .error-icon, .no-data-icon {
      font-size: 64px;
      margin-bottom: 20px;
    }
    
    h3 {
      color: #333;
      margin-bottom: 10px;
      text-align: center;
    }
    
    p {
      color: #666;
      margin-bottom: 20px;
      text-align: center;
      max-width: 500px;
    }
    
    .retry-button, .upload-link {
      background: $primary-gradient;
      color: white;
      border: none;
      padding: 12px 24px;
      border-radius: 25px;
      text-decoration: none;
      font-weight: 500;
      cursor: pointer;
      transition: $transition;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      
      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 15px rgba($primary-color-1, 0.4);
      }
      
      &:disabled {
        opacity: 0.7;
        cursor: not-allowed;
        transform: none;
        box-shadow: none;
      }
    }
  }
  
  .analysis-content {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
    
    .image-section {
      background: $bg-color;
      border-radius: $border-radius-large;
      padding: 30px;
      backdrop-filter: blur(20px);
      box-shadow: $shadow;
      display: flex;
      gap: 30px;
      align-items: center;
      
      .image-container {
        flex-shrink: 0;
        
        .analysis-image {
          max-width: 300px;
          max-height: 400px;
          border-radius: $border-radius-medium;
          box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
          object-fit: cover;
          transition: $transition;
          
          &:hover {
            transform: scale(1.02);
          }
        }
      }
      
